.nav-container {
  background: rgba(196, 196, 196, 0.822) !important;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  position: sticky;
  top: 0;
  z-index: 100;
}
.nav-container .left-container span {
  font-family: "Arial", sans-serif;
  font-size: 24px;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.nav-container .right-container span {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
.nav-container .right-container {
  display: flex;
  gap: 30px;
  margin-right: 15vw;
}
.nav-container .left-container {
  flex: 1;
  margin-left: 20px;
}
.home {
  min-width: 100%;
  position: relative;
  top: 0;
  height: auto;
}
.home img {
  min-width: 100%;
  height: auto;
  /* display: block; */
  /* filter: blur(2px); */
}

.nav-link {
  text-decoration: none;
  color: inherit;
}
.resume-container {
  width: 100%;
  height: auto;
  background: brown;
  display: flex;
  justify-content: center;
}
.resume {
  padding: 20px;
  width: 21cm;
  height: 29.7cm;
  background: rgb(255, 255, 255);
}
.top-container h1 {
  text-transform: uppercase;
  font-family: "Arial", sans-serif;
  letter-spacing: 2px;
  font-weight: 800;
  color: rgb(0, 117, 117);
  margin-bottom: 0;
}

.top-container span {
  display: flex;
  gap: 10px;
}
.summary h4 {
  text-transform: uppercase;
  font-family: "Arial", sans-serif;
  letter-spacing: 1px;
  font-weight: 800;
  color: rgb(0, 117, 117);
  margin-bottom: 0;
}
.experience h4 {
  text-transform: uppercase;
  font-family: "Arial", sans-serif;
  letter-spacing: 1px;
  font-weight: 800;
  color: rgb(0, 117, 117);
  margin-bottom: 0;
}
.experience span b {
  display: flex;
  gap: 7px;
}
.experience span {
  display: flex;
  gap: 20px;
}
.project h4 {
  text-transform: uppercase;
  font-family: "Arial", sans-serif;
  letter-spacing: 1px;
  font-weight: 800;
  color: rgb(0, 117, 117);
  margin-bottom: 0;
}
